/*
 * Element: Buttons
 */
.button {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  background-color: #336699;
  white-space: nowrap;
  user-select: none;
  position: relative;
  font-weight: 400;
  font-family: 'Roboto', 'Helvetica', 'Arial', 'sans-serif';
  transition: background 0.3s, color 0.3s, border-color 0.3s;
  padding: 10px 20px;
  color: #ffffff;
  border: 1px solid transparent;
  text-decoration: none;
}
.button:hover,
.button:focus {
  color: #ffffff;
  background: #264c73;
  text-decoration: none;
  outline: none;
}
.button[disabled],
[disabled] .button {
  cursor: not-allowed;
}
.button--ghost {
  color: #363636;
  border: solid 1px #336699;
  background-color: transparent;
}
.button--ghost:hover,
.button--ghost:focus {
  color: #ffffff;
  background-color: #336699;
}
/*
* Element: Headers
*/
.header {
  font-size: 50px;
  color: #336699;
}
.header__column {
  color: #669933;
}
